@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  min-width: 320px;
  margin: 0; }

img {
  width: 100%;
  height: auto; }

a {
  text-decoration: none;
  color: inherit;
  word-break: break-word; }

.body-lock {
  width: 100%;
  position: fixed;
  overflow-y: scroll; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@keyframes buzz-out {
  10% {
    transform: scale(1.2) translateX(3px) rotate(10deg); }
  20% {
    transform: scale(1.2) translateX(-3px) rotate(-10deg); }
  30% {
    transform: scale(1.2) translateX(3px) rotate(10deg); }
  40% {
    transform: scale(1.2) translateX(-3px) rotate(-10deg); }
  50% {
    transform: scale(1.2) translateX(2px) rotate(5deg); }
  60% {
    transform: scale(1.2) translateX(-2px) rotate(-5deg); }
  70% {
    transform: scale(1.2) translateX(2px) rotate(5deg); }
  80% {
    transform: scale(1.2) translateX(-2px) rotate(-5deg); }
  90% {
    transform: scale(1.2) translateX(1px) rotate(0); }
  100% {
    transform: scale(1) translateX(-1px) rotate(0); } }

@keyframes push {
  50% {
    transform: scale(0.9); }
  100% {
    transform: scale(1); } }

@media (max-width: 1023px) {
  @keyframes push {
    50% {
      transform: scale(0.6); }
    100% {
      transform: scale(1); } } }

.intro {
  display: flex;
  background-color: #000000;
  background-image: url("../img/desktop/bg-main@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .intro {
      background-image: url("../img/desktop/bg-main@2x.jpg"); } }
  @media (max-width: 1023px) {
    .intro {
      background-image: url("../img/tablet/bg-main@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .intro {
      background-image: url("../img/tablet/bg-main@2x.jpg"); } }
  @media (max-width: 767px) {
    .intro {
      background-image: url("../img/mobile/bg-main@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .intro {
      background-image: url("../img/mobile/bg-main@2x.jpg"); } }

.intro__height {
  padding-bottom: 45.73%; }
  @media (max-width: 1023px) {
    .intro__height {
      padding-bottom: 89.09%; } }
  @media (max-width: 767px) {
    .intro__height {
      padding-bottom: 202%; } }

.intro__wrap {
  width: 100%; }

.nav {
  display: flex;
  max-width: 1514px;
  flex-wrap: wrap;
  align-items: center;
  margin: auto;
  padding: 33px 52px 0 50px;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .nav {
      padding: 21px 25px 0 25px; } }
  @media (max-width: 767px) {
    .nav {
      padding: 17px 15px 0 17px; } }

.nav__link {
  display: flex;
  align-items: center;
  margin-bottom: 20px; }

.nav__link--logo {
  min-width: 202px;
  margin-right: 119px; }
  @media (max-width: 1175px) {
    .nav__link--logo {
      margin-right: 30px; } }
  @media (max-width: 1023px) {
    .nav__link--logo {
      min-width: auto;
      margin-right: auto; } }

.nav__link svg {
  flex-shrink: 0; }

@media (max-width: 767px) {
  .nav__link--logo svg {
    width: 38px;
    height: 38px; } }

.nav__company {
  margin-left: 16px;
  cursor: default; }
  @media (max-width: 767px) {
    .nav__company {
      display: none; } }

.nav__company p:first-child {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 600;
  font-size: 25px;
  line-height: 33px; }

.nav__company p:last-child {
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

.nav__decor {
  width: 37px;
  height: 13px;
  margin: 0 auto 20px 0;
  border-bottom: 1px solid #ffffff;
  border-top: 1px solid #ffffff; }
  @media (max-width: 1023px) {
    .nav__decor {
      margin: 0 37px 28px 0; } }
  @media (max-width: 767px) {
    .nav__decor {
      margin: 0 27px 25px 0; } }

.nav__contact {
  display: flex;
  min-width: 430px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin: 0 49px 0 0;
  padding: 0;
  list-style: none; }
  @media (max-width: 1175px) {
    .nav__contact {
      min-width: 380px;
      margin-right: 30px; } }
  @media (max-width: 1023px) {
    .nav__contact {
      margin-bottom: 5px;
      margin-right: 0;
      min-width: auto; } }

.nav__link--phone svg {
  margin-right: 16px; }
  @media (max-width: 1023px) {
    .nav__link--phone svg {
      width: 24px;
      height: 24px;
      margin-right: 0; } }

.nav__link--mail {
  margin-right: 25px; }
  @media (max-width: 1023px) {
    .nav__link--mail {
      margin-right: 30px; } }
  @media (max-width: 767px) {
    .nav__link--mail {
      margin-right: 20px; } }

.nav__link--mail,
.nav__link--phone {
  transition: color 0.3s; }
  .nav__link--mail:hover, .nav__link--mail:focus,
  .nav__link--phone:hover,
  .nav__link--phone:focus {
    color: #00b2ff; }
  .nav__link--mail:active,
  .nav__link--phone:active {
    animation-name: push;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1; }
  .nav__link--mail:hover svg,
  .nav__link--phone:hover svg {
    animation-name: buzz-out;
    animation-duration: 0.3s;
    animation-timing-function: linear;
    animation-iteration-count: 1; }

.nav__link--mail svg {
  margin-right: 20px; }
  @media (max-width: 1023px) {
    .nav__link--mail svg {
      width: 25px;
      height: 19px;
      margin-right: 4px; } }

.nav__btn {
  position: relative;
  padding: 13px 26px;
  margin-bottom: 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 2px solid #ffffff;
  border-radius: 5px;
  background-color: transparent;
  color: inherit;
  cursor: pointer;
  z-index: 0;
  overflow: hidden;
  transition: color 0.3s;
  /*  &:hover,
    &:focus {
      color: $color-blue;
      border-color: $color-blue;
    }*/ }
  .nav__btn::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #00b2ff;
    border-radius: 100%;
    transform: scale(0);
    transition: transform 0.3s ease-out; }
  .nav__btn:hover::before {
    transform: scale(2); }
  .nav__btn:active {
    animation-name: push;
    animation-duration: 0.3s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1; }

@media (max-width: 1023px) {
  .nav__link span,
  .nav__btn {
    display: none; } }
